:root {
  --color: seashell;
  --bg-color: black;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Helvetica, serif;
  font-size: 22px;
  font-weight: bold;
  color: var(--color);
  background-color: var(--bg-color);
  text-align: center;
  letter-spacing: -1px;
}

body {
  margin: 0;
  padding: 0;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 69px;
  border-bottom: 1px solid var(--color);
  padding: 0 10px;
  position: fixed;
  top: 0;
  z-index: 1;
  background-color: var(--bg-color);
}

.logo {
  max-height: 50px;
  width: auto;
  opacity: 0.9;
}

nav {
  display: flex;
  align-items: center;

  a {
    color: var(--color);
    opacity: 0.9;
    transition: all 0.3s;

    &:not(:first-of-type) {
      margin-left: 25px;
    }

    &:hover {
      opacity: 0.5;
    }
  }
}

.mobile-menu-btn {
  @media screen and (min-width: 600px) {
    display: none;
  }
}

main {
  position: relative;
  top: 69px;
}

.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  min-height: 700px;
  margin: 0 50px;
  background-image: url("./images/img-mission-background.jpg");
  background-size: cover;

  &__content {
    width: 100%;
    padding: 10px;
    background-color: var(--bg-color);

    h2,
    h4 {
      opacity: 0.9;
    }

    h2 {
      margin-bottom: 25px;
    }
  }
}
